import logo from './logo.svg';
import './App.css';

import {Wallet, ResponsiveAppBar} from './components';
import {createWeb3modal} from './connect'
import { toHaveErrorMessage } from '@testing-library/jest-dom/dist/matchers';
import { ethers } from 'ethers';
import {AppBar, Box, Grid, Paper, Typography, Button, ThemeProvider, Dialog, DialogTitle, DialogActions, DialogContent, IconButton} from '@mui/material'
import CloseIcon from "@mui/icons-material/Close";
import { BtnTheme } from './theme';

import {useState, useCallback} from "react";
import { CONTRACT_ABI } from './web3/abi';

const web3modal = createWeb3modal();

const BootstrapDialogTitle = (props) => {

  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, pr: 10 }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};

const alive = true;
const contract_addr = "0x07C04a06597E43b3aDBFeeDc5BA249D586341303";

function App() {
  
  const [open, setOpen] = useState(false);
  const [txHash, setTxHash] = useState(null);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const [provider, setProvider] = useState(null);
  const [connectStatus, setConnectStatus] = useState('disconnect');
  const [logBtnText, setLogBtnText] = useState('connect');

  const mint = useCallback(async () => {
    if (!alive) {
    alert('mint not alive!');
    document.location.reload();
    return;
    }
    if (provider) {
      const signer = provider.getSigner();
      if (signer) {
        let contract = new ethers.Contract(contract_addr, CONTRACT_ABI);

        let balance = await signer.getBalance();
        console.log(balance);
        console.log(balance.toString());
        let payAmount = ethers.utils.parseEther('0.05')
        console.log(payAmount);
        console.log(payAmount.toString());
        if (payAmount - balance > 0) {
          alert('not enough balance for mint.');
          document.location.reload();
        }
        contract.connect(signer).mint(1, {
            value: payAmount
        }).then(res => {
          setTxHash(res.hash);
          handleOpen();
        }).catch(e => {
          alert("error occurs: " + e.toString());
          document.location.reload();
        })
      }
    } else {
      alert('connect to metamask first!');
      document.location.reload();
    }
    
  },
  [provider]);
  const whiteMint = useCallback(async () => {
    if (!alive) {
    alert('mint not alive!');
    document.location.reload();
    return;
    }
    if (provider) {
      const signer = provider.getSigner();
      if (signer) {
        let contract = new ethers.Contract(contract_addr, CONTRACT_ABI);

        contract.connect(signer).whitelistMint(1, {
          value: ethers.utils.parseEther('0')
        }).then(res => {
          setTxHash(res.hash);
          handleOpen();
        }).catch(e => {
          alert("error occurs: " + e.toString());
          document.location.reload();
        })
      } else {
        alert('connect to metamask first!');
        document.location.reload();
        
      }
    } else {
      alert('connect to metamask first!');
      document.location.reload();
    }
    
  },
  [provider]);

  const disconnect = async () => {
    console.log('disconnect');
    await web3modal.clearCachedProvider();
    if (provider && provider.provider && typeof provider.provider.disconnect == "function") {
      await provider.provider.disconnect();
    }
    setConnectStatus('disconnect');
    setLogBtnText('connect');
    setTimeout(() => {
      window.location.reload();
    }, 1);
  };


  const updateText = async (provider) => {
    if (provider) {
      console.log('update log btn text');
      const signer = provider.getSigner();
      const address = await signer.getAddress();
      const show_addr = address.substr(0,5) + '...' + address.substr(address.length-5,5);
      setLogBtnText(show_addr);
    }
  };

  const connect = useCallback(async () => {
    const instance = await web3modal.connect();
    console.log(instance);
    
    const newProvider = new ethers.providers.Web3Provider(instance)

    let network = await newProvider.getNetwork();
    console.log('chainId')
    console.log(network.chainId)
    if (network.chainId !== 1) {
        alert("Switch Metamask to ETH Mainnet first!");
        document.location.reload();
    }
    setProvider(newProvider);
    
    instance.on('chainChanged', chainId=> {
      console.log(`chain changed to ${chainId}`);
      const newProvider = new ethers.providers.Web3Provider(instance)
      setProvider(newProvider);
      updateText(newProvider);
    });

    instance.on('accountsChanged', (accounts) => {
      console.log(accounts);
      if (accounts.length > 0) {
        console.log('account changed.');
        const newProvider = new ethers.providers.Web3Provider(instance)
        setProvider(newProvider);
        updateText(newProvider);
      } else {
        disconnect();
      }
    })

    instance.on('disconnect', (code, reason) => {
      console.log(code,reason);
      disconnect();
    })
    console.log(connectStatus);
    setConnectStatus('connected');
    //setLogBtnText(show_addr);
    updateText(newProvider);
  },
  [setProvider]
  );

  return (
    <div className="App">
      {/* <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header> */}
      <ResponsiveAppBar/>
      <Box sx={{width: {xs: '100%', md: '70%', lg:'50%'}, color: 'white', margin: 'auto',  display: 'flex' }}>
        <Grid container spacing={5}>
          <Grid item xs={12}>
            <Box display="flex">
            <Typography variant="h6"
            noWrap
            component="a"
            sx={{
              mt: '100px',
              ml: 'auto',
              mr: 'auto',
              display: { xs: 'flex', md: 'flex' },
              fontFamily: 'Koulen',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: '#ffffff',
              textDecoration: 'none',
              align: 'center',
              zIndex: 2
            }}
            > Libertas Space War
            </Typography></Box>
          </Grid>
          <Grid item xs={12}>
            <Box display="flex">
            <Typography variant="h7"
            component="a"
            sx={{
              mt: '10px',
              ml: 'auto',
              mr: 'auto',
              display: { xs: 'flex', md: 'flex' },
              fontFamily: 'Roboto',
              fontWeight: 700,
              letterSpacing: '.1rem',
              color: '#ffffff',
              textDecoration: 'none',
              align: 'center',
              zIndex: 2
            }}
            > Space War is the second collection of AI-generated artwork by Libertas. Shocked by the chaos and anxiety around the world now, this collection includes 888 unique art pieces and hopes to remind us how beautiful a peaceful world should be. As governments are expanding territories with threats, weapons and even wars, the hearts of people are broken at the same time. We hope to use art to unite and bring peace to all generations.
            </Typography></Box>
          </Grid>
          <Grid item xs={12}>
            <Box display="flex">
            <Typography variant="h7"
            noWrap
            component="a"
            sx={{
              mt: '80px',
              ml: 'auto',
              mr: 'auto',
              display: { xs: 'flex', md: 'flex' },
              fontFamily: 'Roboto',
              fontWeight: 700,
              letterSpacing: '.1rem',
              color: '#ffffff',
              textDecoration: 'none',
              align: 'center',
              zIndex: 2
            }}
            > 0.05 Ether Mint 
            </Typography></Box>
          </Grid>
          <Grid item xs={12}>
            <Box
              width="100%" alignItems="center">
              <ThemeProvider theme={BtnTheme}>
              <Button variant="outlined" color="primary" onClick={mint}>
                Mint
              </Button>
              </ThemeProvider>
            </Box>
          </Grid>
          <Grid item xs={12}>
            <Box display="flex">
            <Typography variant="h7"
            noWrap
            component="a"
            sx={{
              mt: 'auto',
              ml: 'auto',
              mr: 'auto',
              display: { xs: 'flex', md: 'flex' },
              fontFamily: 'roboto',
              fontWeight: 700,
              letterSpacing: '.1rem',
              color: '#ffffff',
              textDecoration: 'none',
              align: 'center',
              zIndex: 2
            }}
            > Free Mint if you hold Spectrum NFTs 
            </Typography></Box>
          </Grid>
          <Grid item xs={12}>
            <Box width="100%" alignItems="center">
              <ThemeProvider theme={BtnTheme}>
              <Button variant="outlined" color="primary" onClick={whiteMint}>
                Whitelist Mint
              </Button>
              </ThemeProvider>
            </Box>
          </Grid>
        </Grid>
      </Box>
      {/* <Box sx={{width: '100%'}}></Box> */}
      <Wallet status={connectStatus} connect={connect} disconnect={disconnect} text={logBtnText}/>
      <Dialog sx={{
        p:2,
      }} onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
          Success
        </BootstrapDialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
            Congrats! click the button below to view tx on etherscan
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={
            () => {
              window.open('https://etherscan.io/tx/' + txHash, '_blank');
            }
          }>
            View On Etherscan 
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default App;
